.user-item {
    margin: 1rem;
    width:min(90%,600px);
    min-width: 17.5rem;
    float:left;
    margin: auto;
  }
  .userItem{
    display: block;
  }
  
  .user-item a {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
    padding: 1rem;
    color: white;
    background: #292929;
  }
  
  .user-item a:hover,
  .user-item a:active {
    background: #ffa600;
  }
  
  .user-item__content {
    padding: 0;
    box-shadow: none;
  }
  
  .user-item__image {
    width: 4rem;
    height: 4rem;
    margin-right: 1rem;
  }
  
  .user-item__info h2 {
    font-size: 1.5rem;
    margin: 0 0 0.5rem 0;
    font-weight: 800;
    color: white;

  }
  
  .user-item:hover h2,
  .user-item:active h2,
  .user-item:hover h3,
  .user-item:active h3 {
    color: white;
    font-weight: 800;
  }
  
  .user-item__info h3 {
    margin: 0;
  }